import React, { Suspense, lazy } from "react";
import { isTagEmbed, isTagShop } from "../../../../actions/api";
const MediaIcon = lazy(() => import("../../Elements/MediaIcon/MediaIcon"));
const Image = lazy(() => import("../../Elements/Media/Image"));
const Video = lazy(() => import("../../Elements/Media/Video"));
const Network = lazy(() => import("../../Elements/Network/Network"));
const Media = ({ itemData, wallID, ThemeID, ownerId, wall }) => {
  const mediaIcon = itemData.hotspot ? false : true
  const isVideo = (itemData.type === 3 || itemData.type === 5) && (wall.ThemeRule.autoPlay == 1 || !isTagShop) ? true : false
  return <div className="tb_gt_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
    {isTagEmbed ? <div className="tb_gt_social_top_">
      <Suspense fallback={<></>}><Network networkClass={'tb_gt_social__ico'} network={itemData.network} isDefault={wall.ThemeRule.iconType === 1 ? true : false} ThemeRule={wall.ThemeRule} /></Suspense>
    </div> : null}
    <Suspense fallback={<></>}><MediaIcon itemData={itemData} IconClass="tb_gt_media_icon" show={mediaIcon} /></Suspense>
    {isVideo ? <Video VideoClass="tb_gt_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={100} isCover={true} muted={true} />
      : <Suspense fallback={<></>}><Image ImageClass="tb_gt_image" data={itemData} wallID={wallID} themeID={ThemeID} ownerId={ownerId} size={100} hotspot={itemData.hotspot ? true : false} /></Suspense>}
  </div>
}
export default Media